}

ul, ol {
  margin: 0;
  padding: 0;
}

ul li {
  list-style-type: none;
}

ol {
  list-style: none;
  counter-reset: li;
}

ol li {
  position: relative;
  padding-left: 20px;
}

ol li:before {
  content: counter(li) ".";
  counter-increment: li;
  padding-right: 6px;
  margin-left: -20px;
}

input, textarea {
  box-sizing: border-box;
}

input[type=text],
input[type=password],
input[type=tel],
input[type=email],
input[type=number] {
  padding: 3px;
  border: 1px solid #999;
  box-shadow: none;
}

input[type=checkbox] {
  margin-right: 10px;
}

select {
  width: 100%;
  padding: 0;
  border-radius: 0;
  height: 1.6em;
  border: 1px solid #999999;
  font-size: .8em;
}

