.l-bodyInner {
  width: 100%;
  height: 100%;
  position: relative;
}

.l-page, .l-gNav {
  position: absolute;
  top: 0;
  left: 0;
}

.l-page {
  z-index: 2;
  width: 100%;
  min-height: 100%;
  background: #fff;
  transition: -webkit-transform 0.2s ease-in-out;
  box-shadow: rgba(48, 48, 48, 0.470588) 0px 0px 8px 2px;
}

.l-page.movemenu {
  -webkit-transform: translateX(70%);
}

.l-page.movemenu--right {
  -webkit-transform: translateX(-70%);
}

.l-pageHeader {
  border-top: 10px solid #000;
}

.l-breadCrumb {
  padding: 10px 0;
  border-bottom: 1px solid #000;
}

.l-breadCrumb__item {
  display: inline;
  line-height: 1.8;
}

.l-breadCrumb__item a {
  text-decoration: underline;
}

.l-breadCrumb__item:before {
  content: ">";
  margin: 0 6px;
}

.l-breadCrumb__item:first-child:before {
  content: "";
  margin: 0;
}

.l-breadCrumb__item--current:after {
  content: "";
}

.l-globalNav {
  z-index: 1;
  width: 70%;
  height: 100%;
  background: #000;
  overflow-x: hidden;
}

.l-globalNav__list {
  border-top: none;
  border-bottom: 1px solid #fff;
}

.l-globalNav__item {
  margin: 0;
  position: relative;
  border-bottom: none;
  border-top: 1px solid #fff;
}

.l-globalNav__item a {
  display: block;
  color: #fff;
  text-decoration: none;
  padding: 15px 0 14px 20px;
  font-size: 16px;
  line-height: 20px;
}

.l-globalNav__item.is-active {
  background: #000;
}

.l-globalNav--right {
  right: 0;
  left: inherit;
  padding-top: 64px;
}

.l-pageHeader {
  border-top: 10px solid #000;
}

.l-pageBody {
  width: 100%;
}

.l-pageBodyIn {
  padding: 20px;
}

.l-mainvisual {
  background: url("img/top/bg_mv.png") no-repeat center top;
}

.l-pageContent__in {
  padding: 20px;
}

.l-sidebar > li {
  margin-top: 40px;
}

.l-sidebar > li:first-child {
  margin-top: 0;
}

.l-pageFooter {
  width: 100%;
  margin-top: 20px;
  padding: 0;
}

.l-goToTop {
  position: fixed;
  bottom: 20px;
  right: 20px;
  -ms-filter: "alpha( opacity=0 )";
  zoom: 1;
  opacity: 0;
}

.l-goToTop a {
  color: #994d00;
}

.twoCol {
  overflow: hidden;
}

.twoCol__fluid {
  overflow: hidden;
  margin-left: 0;
}

.twoCol__fluid p:first-child {
  margin-top: 0;
}

.twoCol__fixed {
  float: left;
  margin-right: 10px;
}

.twoCol__fixed--right {
  float: left;
  margin-right: 10px;
  float: right;
  margin-left: 10px;
  margin-right: 0;
}
