
// Helper
//
// html helper class
//
// Markup: - <p class="tar">テキストを右寄せにします</p>
//
// Styleguide 3
.u {

  &-align {
    &--left {
      text-align: left;
      margin-right: auto; }

    &--right {
      text-align: right;
      margin-left: auto; }

    &--center {
      margin-left: auto;
      margin-right: auto;
      text-align: center; } } }


